<template>
  <div class="flex flex-col items-center justify-center min-h-screen bg-gray-50">
    <div id="qr-reader" class="w-full max-w-sm"></div>
    <p v-if="qrResult" class="mt-4 text-green-700 font-semibold">QR  : {{ qrResult }}</p>
    <button @click="stopScanner" class="mt-6 bg-red-500 text-white px-4 py-2 rounded">
       
    </button>
  </div>
</template>

<script setup>
import { ref, getCurrentInstance, onMounted, onBeforeUnmount } from 'vue'
import { Html5Qrcode } from 'html5-qrcode'
import { useRouter } from 'vue-router'

const { proxy } = getCurrentInstance()

const router = useRouter()
const qrResult = ref(null)
let html5QrCode = null

const startScanner = async () => {
  const qrRegionId = 'qr-reader'
  html5QrCode = new Html5Qrcode(qrRegionId)
  const config = { fps: 10, qrbox: { width: 250, height: 250 } }

  try {
    const devices = await Html5Qrcode.getCameras()
    console.log(' cameras:', devices)
    if (!devices.length) throw new Error('No camera found')
    //       
    const backCam = devices.find((d) => /back|rear|environment/i.test(d.label)) || devices[0]

    await html5QrCode.start(
      { deviceId: { exact: backCam.id } },
      config,
      (decodedText) => {
        qrResult.value = decodedText
        router.replace({ path: '/ChkLst', query: { qr: decodedText } })
        stopScanner()
      },
      (err) => console.warn('QR error', err)
    )
  } catch (err) {
    console.error('  :', err)
    proxy.$alert(
      '  .\n        .'
    )
  }
}

const stopScanner = () => {
  if (html5QrCode) {
    html5QrCode.stop().then(() => {
      html5QrCode.clear()
      html5QrCode = null
    })
  }
}

onMounted(() => {
  startScanner()
})

onBeforeUnmount(() => {
  stopScanner()
})
</script>

<style scoped>
#qr-reader video {
  border-radius: 0.75rem;
}
</style>
